.alarms {
	margin: 0 auto;
	width: $mainWidth;
	margin-bottom: 1.5em;
	padding-bottom: 1em;
	border-bottom: solid 1px $gray;
	h2 {
		width: 100%;
		background-color: $orange;
		color: $white;
		text-align: center;
		font-size: 2em;
		padding: .25em 0;
		box-shadow: 5px 5px 10px rgba(0,0,0,.35);
		margin-bottom: 1em; }

	.row {
		width: 100%;
		display: flex;
		justify-content: space-between;
		margin-bottom: 1.5em;
		.column {
			position: relative;
			flex: 0 1 24%;
			.box {
				background-color: $white;
				border: 1px solid #cb591e;
				margin: 0 auto;
				padding-bottom: 10px;
				text-align: center;
				width: 100%;
				position: relative;
				z-index: 0;
				&:after {
					background-color: transparent;
					border-radius: 0 0 100px 100px;
					box-shadow: 0 10px 15px rgba(0,0,0,.4);
					content: "";
					height: 100px;
					opacity: .6;
					width: 170px;
					position: absolute;
					left: 20px;
					bottom: 0;
					z-index: -1; } }

			img {
				width: 100%;
				max-width: 200px;
				max-height: 200px;
				height: auto; }
			img.moto-x {
				width: 95px;
				height: auto; }
			.title-botton {
				height: 50px;
				width: 100%;
				background-color: rgba($orange, .90);
				position: absolute;
				bottom: 0;
				z-index: 1;
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 0 5px;
				h3 {
					color: $white;
					font-size: 16px;
					text-align: center;
					width: 100%; }
				a.button {
					color: #424242;
					text-decoration: none;
					background-color: $white;
					display: block;
					padding: 5px;
					font-size: 14px;
					position: relative;
					padding-left: 24px;
					margin-left: 5px;
					transition: all .5s;
					&:hover {
						background-color: #424242;
						color: $white; }
					&:before {
						background-color: #424242;
						border-radius: 25px;
						color: $white;
						content: "+";
						height: 18px;
						width: 18px;
						position: absolute;
						top: 4px;
						left: 3px; } } } } } }

.protect {
	margin: 0 auto 2em;
	padding: 1em;
	padding-bottom: 2em;
	border-bottom: solid 1px $gray;
	width: $mainWidth;
	.protect-logo {
		text-align: center;
		margin-bottom: 1em; }
	.banners {
		display: flex;
		justify-content: space-between;
		.banner-v2,
		.banner-v4 {
			width: 49%; }
		.banner-v2 img,
		.banner-v4 img {
			max-width: 490px;
			width: 100%;
			box-shadow: 0 5px 10px rgba(0,0,0,.5); } } }

.logos-wrapper,
.banners-wrapper {
	margin: 0 auto;
	width: $mainWidth;
	display: flex; }

.logos-wrapper {
	align-items: center;
	padding-bottom: 1em;
	.ontas-logo,
	.directed-logo {
		text-align: center;
		width: 50%; } }

.banners-wrapper {
	justify-content: space-between;
	padding: 0 1em 1em;
	padding-bottom: 2em;
	border-bottom: solid 1px $gray;
	.ontas-banner,
	.viper-banner {
		width: 49%; }
	.ontas-banner,
	.viper-banner {
		img {
			max-width: 470px;
			width: 100%;
			box-shadow: 0 5px 10px rgba(0,0,0,.5); } } }

.antiassault-film {
	align-items: center;
	border-bottom: solid 1px $gray;
	color: $grayText;
	margin: 2em auto 1.5em;
	padding-bottom: 1em;
	width: $mainWidth;
	display: flex;
	justify-content: space-between;
	.information {
		width: 68%;
		h2, h3 {
			font-weight: bold; }
		h2 {
			font-size: 1.2em;
			margin-bottom: 2em; }
		h3 {
			font-size: 1.1em;
			margin-bottom: .5em;
			span {
				margin-right: .5em;
				color: $orange; } }
		p {
			font-size: 17px;
			margin: .5em 0 2em 1em; } }
	.side {
		width: 30%;
		img {
			display: block;
			max-width: 305px;
			margin-bottom: 1em;
			width: 100%; }
		p {
			color: $grayText;
			margin-bottom: 1em;
			text-align: justify; } } }

.max-limit {
	margin: 0 auto;
	width: $mainWidth;
	color: $grayText;
	margin-bottom: 2em;
	display: flex;
	justify-content: space-between;
	p {
		margin-bottom: 1em; }
	.information {
		width: 53%;
		p {
			text-align: justify; }
		.brands-lists {
			display: flex;
			ul {
				flex: 1 0 30%;
				margin: 1em 0 1.5em 1em;
				justify-content: space-between;
				align-items: center; }
			li {
				margin-bottom: .5em;
				list-style-type: disc;
				color: $orange;
				&:last-child {
					margin: 0; }
				span {
					color: $grayText; } } } }
	.side {
		width: 45%;
		img {
			margin-bottom: 1em;
			box-shadow: 0 5px 10px rgba(#000, .5); }
		ul {
			margin: .5em 0 1em 1em;
			li {
				list-style-type: disc;
				color: $orange;
				margin: .5em 0;
				span {
					color: $grayText; } } } } }
